<template>
  <div
    class="
      container
      jumbotron jumbotron-fluid
      mb-3
      pt-0
      pb-0
      bg-gray
      position-relative
    "
    style="margin-bottom: 5px"
    v-bind:id="`div-${categoryInfo.sname}`"
  >
    <div class="category-header">
      <h5 class="mt-1 btn btn-dark font-weight-bold">
        <span style="padding-bottom: 5px">{{ categoryInfo.aname }}</span>
      </h5>
      <input
        class="keyword-input"
        type="text"
        placeholder="标题关键字"
        v-model="keyword"
      />
    </div>
    <div class="mb-1 d-flex justify-content-center border-bottom"></div>
    <ol class="list-featured" v-bind:id="['ol-files-' + categoryInfo.sname]">
      <li
        class="mb-md-2"
        v-for="(file, index) in currentPageFiles"
        :key="file.name"
      >
        <span class="li-file-number">{{ baseIndex + index + 1 }}</span>
        <div class="pl-3 justify-content-center">
          <h6 class="font-weight-bold" style="margin-bottom: 0">
            <a
              class="text-dark"
              v-bind:href="`/#/blog${categoryInfo.sname}/${file.name}`"
              style="cursor: pointer"
              v-on:click.prevent="(event) => viewBlog(file)"
              >{{ file.name }}</a
            >
          </h6>
          <div class="card-text text-muted small">Fantasy955</div>
          <small class="text-muted"
            >{{ file.updatetime.year }}年{{ file.updatetime.mon }}月{{
              file.updatetime.day
            }}日 更新</small
          >
        </div>
      </li>
    </ol>
    <div class="card-text mr-1" v-if="readMore">
      <button
        type="button"
        class="btn btn-sm btn-outline-primary"
        v-on:click="(event) => showAll()"
      >
        View ALL
      </button>
    </div>
    <div class="card-text mr-1 pb-2" v-if="layaside">
      <button
        type="button"
        class="btn btn-sm btn-outline-primary"
        v-on:click="(event) => layAside(event)"
      >
        收起
      </button>
    </div>
    <div
      class="row pl-3 justify-content-center pb-2"
      style="gap: 20px; align-items: center"
      v-if="!layaside"
    >
      <p class="text-muted card-text small" style="padding: 0; margin: 0">
        总共 {{ totalPage }} 页, 当前第 {{ pageIndex }} 页
      </p>
      <div>
        <!-- :style="{'pointer-events': pageIndex>1 ? 'auto':'none'}" -->
        <button
          type="button"
          v-on:click="
            (event) => {
              pageIndex--;
              baseIndex = (pageIndex - 1) * max_file_items;
            }
          "
          class="btn btn-sm btn-outline-primary mr-1"
          :class="{ 'button-disabled': pageIndex <= 1 }"
        >
          上一页
        </button>
        <button
          type="button"
          v-on:click="
            (event) => {
              pageIndex++;
              baseIndex = (pageIndex - 1) * max_file_items;
            }
          "
          class="btn btn-sm btn-outline-primary"
          :class="{ 'button-disabled': pageIndex >= totalPage }"
        >
          下一页
        </button>
      </div>
    </div>
  </div>
</template>

<script setup>
import uConfig from "../config";
import { defineProps, onMounted, computed, ref, watch } from "vue";
import { useRoute, useRouter } from "vue-router";

const route = useRoute();
const router = useRouter();

const probs = defineProps({
  categoryInfo: {
    type: Object,
    required: true,
  },
});

const keyword = ref("");

var pageIndex = 1;
pageIndex = ref(pageIndex);

var max_file_items = uConfig.max_file_items;
max_file_items = ref(max_file_items);

var layaside = false;
layaside = ref(layaside);

const baseIndex = ref(0);

const filteredFiles = computed({
  get() {
    return probs.categoryInfo.files.filter((item) => {
      return item.name.toLowerCase().indexOf(keyword.value.toLowerCase()) != -1;
    });
  },
});

const readMore = computed({
  get() {
    return filteredFiles.value.length > max_file_items.value;
  },
  set(newValue) {
    console.log("set");
  },
});

const totalPage = computed(() => {
  return Math.max(
    1,
    Math.ceil(filteredFiles.value.length / max_file_items.value)
  );
});

const currentPageFiles = computed(() => {
  if (max_file_items.value != Infinity) {
    let start = (pageIndex.value - 1) * max_file_items.value;
    return filteredFiles.value.filter((item, index) => {
      return index >= start && index < start + max_file_items.value;
    });
  } else {
    return filteredFiles.value;
  }
});

function showAll() {
  baseIndex.value = 0;
  max_file_items.value = Infinity;
  layaside.value = true;
  pageIndex.value = 1;
}

function layAside(event) {
  baseIndex.value = 0;
  max_file_items.value = uConfig.max_file_items;
  layaside.value = false;
  let div = document.getElementById(`div-${probs.categoryInfo.sname}`);
  div.scrollIntoView({ behavior: "smooth" });
}

function viewBlog(file) {
  router.push({
    name: "blog",
    params: {
      path: file.relapath,
      categorySname: probs.categoryInfo.sname,
      blogName: file.name,
    },
  });
}

onMounted(() => {
  // console.log('mounted');
});
</script>

<style scoped>
.category-header {
  display: flex;
  column-gap: 10px;
  align-items: center;
  flex-wrap: wrap;
}

.page-control {
  width: auto;
}

.keyword-input {
  border-top-left-radius: 1;
  border-bottom-left-radius: 1;
  /* margin-left: 2rem; */
  padding: 0.4375rem 0.75rem;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.4285714;
  color: #232e3c;
  background-color: hsla(0, 0%, 100%, 0.651);
  background-clip: padding-box;
  border: 1px solid #44494e;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 4px;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.li-file-number {
  font-weight: bold;
  font-size: 3rem;
  margin-right: 0.5rem;
  font-family: "Abril Fatface", serif;
  line-height: 1;
}

.button-disabled {
  pointer-events: none;
}

button.button-disabled {
  cursor: not-allowed;
}

ol.list-featured li:before {
  display: none;
}
</style>